<template>
  <div>
    <div class="header">
      <div class="wrap flex">
        <h4>
          <!--<small></small>-->
        </h4>
        <div>
        </div>
      </div>
    </div>

    <div class="wrap">
      <div class="photo-row">
        <p></p>
        <input type="text" class="title" v-model="title" @keyup="input">
      </div>
      <div class="photo-row">
        <p></p>
        <textarea placeholder="" rows="4" v-model="content" @keyup="input"></textarea>
      </div>
      <div class="photo-row">
        <p></p>
        <div class="btn-wraper" style="position: relative;" id="container">
          <ul class="file-list clearfix">
            <li v-for="(item, index) in momentImg">
              <div class="progress">
                <div class="progress-wrap">
                  <span :class="{ active:callbackImg[index] }"></span>
                </div>
              </div>
              <img :src="item" alt="">
            </li>
            <li id="pickfiles" class="add-img"><i class="icon-add"></i></li>
          </ul>
        </div>
      </div>
      <div class="photo-row">
        <button class="submit" :disabled="!commit" :class="{active:commit}" @click="push"></button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        momentImg: [],
        callbackImg: [],
        commit:false,
        title:"",
        content:"",
      }
    },
    //
    props: {
      Authid: {
        type: String
      }
    },
    mounted() {
      var uploader = Qiniu.uploader({
        runtimes: 'html5,flash,html4', //,
        browse_button: 'pickfiles', //****
        uptoken: 'T6D-Sh74Dp7-1N_gZ2X14MY_PORKAXuhyh7ucawg:nVoiO4H4aV10k2woXnliqyZ9-oo=:eyJzY29wZSI6IndlYnMiLCJkZWFkbGluZSI6NDYzODg3NzI2MSwicmV0dXJuQm9keSI6IntcImtleVwiOiAkKGtleSksIFwiaGFzaFwiOiAkKGV0YWcpLCBcIndcIjogJChpbWFnZUluZm8ud2lkdGgpLCBcImhcIjogJChpbWFnZUluZm8uaGVpZ2h0KX0ifQ==',
        domain: 'http://ov0xnpdna.bkt.clouddn.com/', //bucket ****
        get_new_uptoken: false, //token
        container: 'container', //DOM IDbrowser_button
        max_file_size: '10mb', //
        flash_swf_url: 'Moxie.swf', //flash,
        max_retries: 3, //
        dragdrop: false, //
        drop_element: 'container', //ID
        chunk_size: '4mb', //
        auto_start: true, //
        init: {
          'FilesAdded': (up, files) => {
            for (let file of files) {
              let image = new Image();
              let preloader = new mOxie.Image();
              preloader.onload = () => {
                preloader.downsize(150, 150);
                this.momentImg.push(preloader.getAsDataURL());
              };
              preloader.load(file.getSource());
            }
          },
          'Key': (up, file) => {
            // key
            //  unique_names: false , save_key: false 
            let time = new Date().getTime();
            let key = time + ".jpg";
            return key
          },
          'FileUploaded': (up, file, info) => {
            let domain = up.getOption('domain');
            let res = JSON.parse(info.response);
            let sourceLink = domain + res.key; //Url
            this.callbackImg.push({
              src:sourceLink,
              w:res.w,
              h:res.h
            });
            this.input();
          },
        }
      });
    },
    methods:{
      input() {
        this.commit = this.title && this.callbackImg.length>0 ? true : false;
      },
      push(){
        let photo = Bmob.Object.extend('photo');
        let diary = new photo();
        diary.set('title', this.title);
        diary.set('detail', this.content);
        diary.set('pic', this.callbackImg);
        diary.set('look', 0);

        var post = Bmob.Object.createWithoutData("User", this.Authid);
        diary.set("user", post);

        diary.save(null, {
          success: (res) => {
            console.log(res)
            alert("")
          },
          error: (gameScore, error) => {
            alert("")
          }
        });
      }
    }
  }
</script>



